:root {
  --gray-100: #121213;
  --gray-200: #333;
  --gray-800: #ccc;
  --gray-900: #eee;
  --green: #3cd23c;
  --yellow: #b09e08;
  --red: #d00;
  --box-border: 2px;
}

body {
  background-color: var(--gray-100);
  color: var(--gray-900);
}

main {
  margin-left: auto;
  margin-right: auto;
  max-width: 400px;
}

h1 {
  text-align: center;
}

summary {
  cursor: pointer;
}

.rules {
  margin-top: 1em;
  color: var(--gray-800);
  font-size:  1.3em;
}

.row {
  display: flex;
  justify-content: center;
}

.row__letter {
  height: 50px;
  width: 50px;
  border: var(--box-border) solid var(--gray-800);
  text-align: center;
  font-size: 45px;
  text-transform: uppercase;
  line-height: 1;
  margin: 5px;
  background-color: var(--gray-200);
  color: var(--gray-800);
}

.row__letter--correct {
  background-color: var(--green);
  color: white;
  box-shadow: 0 0 10px var(--green);
}

.row__letter--incorrect {
  background-color: var(--yellow);
  color: var(--gray-100);
  border-color: var(--gray-100);;
}

.guess {
  display: flex;
  margin-top: 1em;
}

.guess__input {
  flex-grow: 1;
  font-size: 1em;
}

.invalid {
  text-align: center;
  border: var(--box-border) solid var(--red);
  background-color: #311;
  width: fit-content;
  padding: 1em;
  margin-left: auto;
  margin-right: auto;
}

.result {
  font-size: 2.5em;
  text-align: center;
  margin: 1em 0;
}

.play-again {
  text-align: center;
}

.play-again__button {
  font-size: 2em;
  padding: 0.25em 0.5em;
}

.score {
  text-align: center;
  margin-bottom: 1em;
  font-size: 1.2em;
}
